// ------ OR -------

.separator_OR {
  padding: 0px 40px;

  p {
    position: relative;
    margin-top: 20px;
    text-align: center;
    position: relative;
    font-size: 12px;
    font-weight: bold;
    color: #a3a2a2;
  }

  .one {
    margin-top: 0;
  }
  .one:before {
    content: "";
    display: block;
    border-top: solid 1px #dbd9d9;
    width: 100%;
    height: 1px;
    position: absolute;
    top: 50%;
    z-index: 1;
  }

  .one span {
    background: #fff;
    padding: 0 20px;
    position: relative;
    z-index: 5;
  }
}

// Inputs in Forms

.form_container_inputs {
  display: flex;
  flex-direction: column;
  margin-top: 15px;

  input,
  button {
    width: 100%;

  }

  button {
    cursor: pointer;
  }
}

.form_container-input_label_div {
  margin: 0 40px 6px;
  border: 1px solid rgba(var(--ca6, 219, 219, 219), 1);

  label {
    display: flex;
    height: 36px;
    margin: 0;
    padding: 0;
    position: relative;
    color: rgba(var(--i1d, 38, 38, 38), 1);

    input {
      padding: 7px 0 7px 7px;
      font-size: 12px;
      background: rgba(var(--b3f, 250, 250, 250), 1);
      border: none;
      cursor: text;
    }

    input:focus {
      outline: none;
    }
  }
}

// Submit button in forms

.form_container-submit_div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px 40px;

  button {
    padding: 8px 0;
    background-color: #0095f6;
    border: 1px solid #dbd9d9;
    border-radius: 5px;
    font-size: 14px;
    font-weight: bold;
    color: #ffffff;
  }
}
